<template>
    <div>
        <div class="find-top clearfix">
            <van-search 
                v-model="value" 
                show-action
                placeholder="请输入搜索关键词"  
                shape="round" background="transparent" 
                @input="search"
                @cancel="onCancel"
            />  
            <div class="find-list">
                <div class="find-one" v-for="(item,index) in searchData" :key="index" @click="goa(item)">{{item}}</div>
            </div>
            <div class="hot-tip">热搜榜</div>
            <div class="topdetail-list">
                <div class="list-one clearfix" v-for="(item,index) in books" :key="index" @click="goBooktedail(item._id)">
                    <div class="one-img fl">
                        <img :src="item.cover" class="auto-img" alt="">
                    </div>
                    <div class="fl one-right">
                        <div class="one-title">
                            {{item.title}}
                        </div>
                        <div class="one-detail">
                            {{item.shortIntro}}
                        </div>
                        <div class="one-last">
                            <div class="fl ">作者:{{item.author}}</div>
                            <div class="fr">{{item.majorCate}}</div>
                        </div>
                    </div>
                
            </div>
        </div>     
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                value:"",
                searchData:[],
                _id:"",
                books:[],
                title:""
            }
        },
        created(){
            this.gettopDetail('5a6844f8fc84c2b8efaa8bc5')
        },
        methods:{
            search(){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/auto-complete",
                        params: {
                            query:this.value
                        }
                    }).then(result => {
                        this.$toast.clear(); 
                        
                        this.searchData = result.data.keywords

                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
             goa(item){
                
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "search?keyword="+item,
                        params: {
                            
                        }
                    }).then(result => {
                        this.$toast.clear(); 
                        
                        let id = result.data.books[0]._id
                        this.$router.push({name:"Bookdetail",query:{id}})

                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            onCancel(){
                this.$router.go(-1)
            },
            imgKey(value) {
                return unescape(value).slice(7);
            },
            gettopDetail(id){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/rank/"+id,
                        
                    }).then(result => {
                        this.$toast.clear(); 
                      
                        for(let i = 0 ; i < result.data.ranking.books.length ; i++){
                            result.data.ranking.books[i].cover = this.imgKey(result.data.ranking.books[i].cover)
                        }
                        this.books = result.data.ranking.books

                        
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            goBooktedail(id){
                this.$router.push({name:"Bookdetail",query:{id}})
            }

        }
    }
</script>

<style lang="less" scoped>
.hot-tip{
    text-align: center;
    font-size: 16px;
}
.find-top{
    background-color: #fff;
    position: relative;
    .find-list{
        background-color: #fff;
        position: absolute;
        width: 85%;
        left: 30px;
        top:60px;
        background-color: rgba(255, 255, 255, .8);
        border-radius: 10px;
        z-index: 999;
        >.find-one{
            padding: 5px 10px;
            font-size: 14px;
        }
    }
    padding: 15px;
    margin-top: 10px;
    .top-x{
        width: 90%;
        overflow-x: scroll;
        overflow-y: hidden;
    }
    .top-x::-webkit-scrollbar {
        display: none;
    }
    .top-list{
        
        text-align: center;

        >div{
            font-size: 14px;
            font-weight: 700;
            color: #b6b9bf;
            margin-left: 10px;
            margin-right: 10px;
            
            &.active{
                font-size: 20px;
                color: black;
            }
        }
    }
}
.topdetail-list{
    margin-top: 10px;
    .list-one{
        border-bottom: 1px solid #ccc;
        padding: 15px  ;
    }
    .one-img{
        width: 85px;
    }
    .one-right{
        width: calc(~"100% - 85px");
    }
    .one-title{
        font-size: 16px;
        font-weight: 700;
        padding: 5px;
    }
    .one-detail{
        padding: 5px 10px;
        color: #ccc;
        height: 46px;
        overflow: hidden;
        text-overflow:ellipsis;
	    white-space:nowrap
    }
    .one-last{
        color: #feb67e;
        padding: 10px;
    }
}
</style>